
import Vue from 'vue';
import Comanda from './components/Comanda.vue';

export default Vue.extend({
    name: 'Cuina-view',
    components: { Comanda },

    data: () => ({
        //
        comandes: [
            {
                id: 1,
                plats: [
                    {
                        nom: 'Entrecot',
                        anotacions: 'Molt fet',
                        cuinat: false,
                    },
                    {
                        nom: 'Entrecot',
                        anotacions: 'Poc fet',
                        cuinat: false,
                    },
                    {
                        nom: 'Entrecot',
                        anotacions: 'Al punt',
                        cuinat: false,
                    },
                ],
                enviat: false,
            },
            {
                id: 2,
                plats: [
                    {
                        nom: 'Entrecot',
                        anotacions: 'Molt fet',
                        cuinat: false,
                    },
                ],
                enviat: false,
            },
            {
                id: 3,
                plats: [
                    {
                        nom: 'Amanida de tomaquet amb ceps i formatge de cabra',
                        anotacions: 'Molt fet',
                        cuinat: false,
                    },
                ],
                enviat: false,
            },
        ],
    }),
    methods: {
        eliminarComanda(i: number) {
            this.comandes.splice(i, 1);
        },
        marcarComEnviat(comanda: any) {
            comanda.enviat = true;
        },
    },
    computed: {
        comandesPendents() {
            return this.comandes.filter((comanda) => {
                return !comanda.enviat;
            });
        },
        comandesEnviades() {
            return this.comandes.filter((comanda) => {
                return comanda.enviat;
            });
        },
    },
});
